.login-page {

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: url(../../assets/img/login-blur.jpg);

    .login-container {
        .login-signup {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            width: 450px;
            height: 470px;
            background: #111;
            border-radius: 0.625rem;
            text-align: center;
            padding: 40px;
            box-shadow: 0 0 7pxhsla(0, 0%, 67.8%, .23921568627450981);

            .login-form {
                display: grid;
                place-items: center;
                grid-gap: 20px;
            }

            input {
                width: 80%;
                height: 35px;
                color: #fff;
                background-color: #3a3a3a;
                align-items: center;
                justify-content: space-around;
                padding-inline-end: 5px;
                border-radius: 0.4375rem;
                border: none;
            }

            button {
                color: #fff;
                width: 80%;
                height: 2.8125rem;
                border-radius: 1.25rem;
                background-color: #1aa049;
                font-size: 1.2rem;
                text-transform: uppercase;
                border: none;
            }

                .btn-link{
                    color: #fff;
                    font-size: 1.125rem;
                    text-decoration: underline;
                    background-color: transparent;
                    border: none;
                    margin-bottom: 3px;
                }
              
                
            }

        .signup-form {
            display: grid;
            place-items: center;
            grid-gap: 20px;
        }

    }

}
.username{
    display: flex;
    align-items: center;
}