.station-list-main-container {
    display: grid;
    padding: 0 32px;
    background-color: $transparent;
    margin-top: 20px;

    .see-all-station-list {
        display: flex;
        margin-bottom: 10px;
        justify-self: end;
        width: 100%;
        justify-content: space-between;

        >* {
            &:hover {
                text-decoration: underline;
                cursor: pointer;
            }
        }

        div {
            justify-self: start;
            font-size: 24px;
            font-weight: 700;
        }

        span {
            cursor: pointer;
        }

    }

    .station-preview {
        background-color: $accent-clr;
        padding: 16px;
        border-radius: 5px;
        height: 100%;

        .play-song-tool-bar {
            bottom: 157px;
            opacity: 0;
            cursor: default;
            box-shadow: 0 8px 8pxrgba(0, 0, 0, .3);
            left: 69%;
            position: relative;
            transform: translateY(8px);
            transition: all .3s ease;

        }

        &:hover {
            cursor: pointer;
            background-color: $third-clr;

            .play-song-tool-bar {
                opacity: 1;
            }
        }
    }

    .station-list-container {
        margin-bottom: 14px;
        grid-gap: 12px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));

        .station-preview {
            &:hover {
                .play-btn-preview {
                    opacity: 1;
                }
            }
        }

        // height: 322px;
        // overflow-y: hidden;
        .img-details-container {
            height: 200px;
            margin-bottom: 16px;
            position: relative;

            .play-btn-preview {
                border-radius: 500px;
                bottom: 25px;
                box-shadow: 0 8px 8pxrgba(0, 0, 0, .3);
                opacity: 0;
                right: 8px;
                pointer-events: none;
                position: absolute;
                transform: translateY(8px);
                transition: all .3s ease;
                z-index: 2;

            }

            .img-details-new-playlist {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                border-radius: 4px;
                background-color: #333333;
            }

            .img-details {
                background-color: rgba(115, 104, 84, 0.371);
                border: 4px;
                margin-bottom: 16px;
                box-shadow: 0 8px 24px rgb(0 0 0 / 50%);
                border-radius: 5px;
                width: 100%;
                height: 190px;
                object-fit: cover;
            }
        }

        .station-preview-artist {
            min-height: 62px;

            .station-preview-label {
                color: $main-txt-clr;
                font-size: 1rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                font-weight: 700;
                padding-block-end: 4px;
            }

            .station-preview-artist-name {
                color: $sec-txt-clr;
                font-size: 0.875rem;
            }

        }

    }
}

@media (max-width: 600px) {
    .station-list-main-container {
        padding: 0 10px;
    }

    .station-list-container {

        .img-details-container {
            width: 200px;
        }

        display: flex;
    }
}

@media (min-width: 600px) {
    .station-list-container {
        display: grid;
    }
}